<template>
  <div id="CoursewareView">
    <head-top title="文件预览"></head-top>
    <h1>{{name+'.'+fileExt}}</h1>
    <r-file-preview :fileExt="fileExt" :fileId="id"></r-file-preview>
  </div>
</template>

<script>
import headTop from "@/components/headTop";
import RFilePreview from "@/components/RFilePreview";
export default {
  name: "",
  components: { headTop, RFilePreview },
  props: {},
  data() {
    return {
      id: this.$route.params.id,
      fileExt: this.$route.params.fileExt,
      name: this.$route.params.name,

      dataList: [],
      previewUrl: "",
      loading: false,
      diaologShow: false
    };
  },
  created() {},
  mounted() {
    this.$nextTick(() => {});
  },
  methods: {},
  computed: {},
  watch: {}
};
</script>

<style lang='less' scoped>
#CoursewareView {
  width: 100%;
   height: 100%;
  // height: calc(100vh - 4.5rem);
  overflow-y: auto;
  word-break: break-all;
  // background: #fff;
  padding-bottom: 5vw;
}
</style>


